body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@media (min-width: 960px) {
  .App .entry.wide {
    width: 100%; } }

.keyboard-holder {
  position: relative;
  min-height: 500px;
  background-color: #271768; }
  .keyboard-holder .keyboard {
    background-color: #0d0f3d;
    position: absolute;
    max-width: 270px;
    border-radius: 50px 50px 50px 50px;
    max-height: 250px;
    margin: auto;
    left: 0;
    bottom: 0;
    right: 0;
    top: 0;
    background-repeat: no-repeat;
    background-image: radial-gradient(circle at -9090%, #002155 99%, transparent 23%), radial-gradient(circle at 50% -9120%, #002155 99%, transparent 97%, transparent 36%), radial-gradient(circle at 9090%, #002155 99%, transparent 23%), radial-gradient(circle at 50% 9120%, #002155 99%, transparent 23%); }
    .keyboard-holder .keyboard:after {
      content: '';
      height: 10px;
      width: 10px;
      border-radius: 50%;
      background-color: green;
      position: absolute;
      bottom: 5px;
      right: 40px;
      box-shadow: -16px 0px 0 red, -34px 0px yellow; }
    .keyboard-holder .keyboard:before {
      content: '';
      color: green;
      white-space: pre;
      font-weight: 600;
      font-family: monospace;
      font-size: large;
      position: absolute;
      top: 44px;
      left: 42px;
      -webkit-animation: hi-neo 8s infinite;
              animation: hi-neo 8s infinite; }

